.qwidget-popup{background-color:#fff;margin:0;padding:0;font-family:'Zilla Slab', serif;border:3px solid #000;position:fixed;bottom:2rem;right:2rem;overflow:hidden;width:240px;z-index:10000;box-shadow:3px 3px 15px rgba(0,0,0,0.4)}.qwidget-popup__close_button{-webkit-appearance:none;-moz-appearance:none;background:transparent;border:0;font-size:26px;cursor:pointer;position:absolute;padding:0;right:5px;top:5px;text-align:center;line-height:20px;width:20px;outline:none;transition:all 0.5s cubic-bezier(0.19, 1, 0.22, 1)}.qwidget-popup__close_button:hover{background:#000;color:#fff}.qwidget-popup__wrapper{padding:15px}.qwidget-popup__message{padding:10px 0 10px 0}.qwidget-popup__message__text{font-family:'Merriweather', 'Montserrat', serif;font-weight:400;font-size:16px;line-height:1.4}.qwidget-popup__button{cursor:pointer;font-family:'Montserrat', serif;font-weight:700;line-height:1.333;transition:all 0.5s cubic-bezier(0.19, 1, 0.22, 1);background:transparent;border:solid 3px #000;display:block;padding:0.5em 1em;text-align:center;text-decoration:none;font-size:18px;color:#000}.qwidget-popup__button:last-of-type{margin-top:10px}.qwidget-popup__button:active,.qwidget-popup__button:hover{background:#000;color:#fff}
